.PageWrapper {
  display: -ms-flexbox;
  display: flex;
  height: calc(100vh - 64px) !important;
  overflow: hidden !important; }

.MapWrapper {
  width: 100%; }

.MapContainer {
  display: block;
  padding-left: 380px; }

.MapElement {
  height: calc(100vh - 64px);
  width: 100%; }

.InfoWindowImage {
  display: block;
  margin: auto;
  width: 200px;
  cursor: pointer; }

.Loading {
  padding-top: 100px !important;
  margin: auto !important; }

@media only screen and (max-width: 768px) {
  .AdventureList {
    width: 100%;
    -ms-flex-positive: 1;
        flex-grow: 1; }
  .AdventureListMobile {
    width: 100vw !important;
    display: block !important;
    overflow: scroll !important;
    height: 35vh !important; }
  .MapWrapperMobile {
    -ms-flex-positive: 1 !important;
        flex-grow: 1 !important;
    height: 45vh;
    display: block; }
  .MapContainer {
    padding-left: 0;
    height: 100%; }
  .MapElement {
    height: 100%; } }



